.container {
    max-width: 1200px;
    width: 100%;
    margin: 0 auto;
    padding: 0 15px;
    box-sizing: border-box;
}

.container-fluid {
    width: 100%;
    margin: 0 auto;
    padding: 0;
    box-sizing: border-box;
}

.row {
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    position: relative;
    box-sizing: border-box;
}

.col {
    padding: 10px;
    box-sizing: border-box;
}

